body {
  background-color: #f0eff5;
  font-family: Arial, Helvetica, sans-serif;
}
#app {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

/*头部统一样式*/
header {
  height: 40px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  text-align: center;
  background: linear-gradient(90deg, #ff4b3e, #fb2351);
}
header > h1 {
  line-height: 40px;
  font-size: 15px;
  font-weight: normal;
  color: #fff;
  text-align: center;
}
header > .return {
  display: block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
}
header > .return > i {
  color: #fff;
  font-size: 2rem;
}
section {
  position: fixed;
  top: 40px;
  left: 0;
  right: 0;
  bottom: 0px;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
section.has-bottom {
  padding-bottom: 4rem;
}

/*banner*/
.swiper-container {
  border-radius: 5px;
}
.swiper-pagination-bullet-active {
  background-color: #dc1651;
}

/*列表统一样式*/
.common-list li {
  display: flex;
  align-items: center;
  padding: 1rem 0;
  color: #48484c;
  font-size: 1.4rem;
  border-bottom: 1px solid #e3e3e3;
  background-color: #fff;
}
.common-list li:last-child {
  border: 0;
}
.common-list li .content {
  display: block;
  flex: 1;
  font-size: 14px;
}
.common-list li .icon-jiantou {
  font-size: 15px;
  color: #ccc;
}
.padding {
  padding-left: 5px;
  padding-right: 5px;
}
.card {
  border-radius: 10px;
  overflow: hidden;
  padding: 0 20px;
  background-color: #fff;
}
.common-button {
  display: block;
  text-align: center;
  height: 40px;
  line-height: 40px;
  border-radius: 5px;
  background: linear-gradient(90deg, #ff4b3e, #fb2351);
  color: #fff;
  font-size: 15px;
}

/*轮播组件*/
.banner img {
  vertical-align: top;
  width: 100%;
}
.my-swipe .mint-swipe-indicators {
  position: absolute;
  bottom: 10px;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
}
.my-swipe .mint-swipe-indicator {
  width: 8px;
  height: 8px;
  display: inline-block;
  border-radius: 100%;
  background: #000;
  opacity: 0.2;
  margin: 0 3px;
}
.my-swipe .mint-swipe-indicator.is-active {
  background: #dc1651;
  opacity: 0.86;
}

